"use client";

import { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import Link from 'next/link';
import Header from '@/components/ui/Header';
import Footer from '@/components/ui/Footer';
import ProductList from '@/components/ui/ProductList';
import { getProductsByCategory, Product } from '@/lib/products';

export default function CategoryPage() {
  const params = useParams();
  const category = params.category as string;
  
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Format category name for display
  const categoryName = category ? category.charAt(0).toUpperCase() + category.slice(1) : '';

  useEffect(() => {
    async function fetchCategoryProducts() {
      if (!category) return;
      
      try {
        setLoading(true);
        setError(null);
        const categoryProducts = await getProductsByCategory(category);
        setProducts(categoryProducts);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to load products');
      } finally {
        setLoading(false);
      }
    }

    fetchCategoryProducts();
  }, [category]);

  return (
    <>
      <Header />
      
      <main className="flex-1">
        {/* Breadcrumb */}
        <section className="bg-gray-50 py-4">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <nav className="flex items-center space-x-2 text-sm">
              <Link href="/" className="text-gray-600 hover:text-gray-900 transition-colors">
                Home
              </Link>
              <span className="text-gray-400">‚Üí</span>
              <span className="text-gray-900 font-medium">{categoryName}</span>
            </nav>
          </div>
        </section>

        {/* Category Header */}
        <section className="bg-white py-12">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="text-center">
              <h1 className="text-4xl font-bold text-gray-900 mb-4">
                {categoryName}
              </h1>
              <p className="text-lg text-gray-600 max-w-2xl mx-auto">
                {getCategoryDescription(category)}
              </p>
            </div>
          </div>
        </section>

        {/* Products Section */}
        <section className="py-8 bg-gray-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            {loading && (
              <div className="text-center py-12">
                <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
                <p className="mt-4 text-gray-600">Loading {categoryName.toLowerCase()}...</p>
              </div>
            )}

            {error && (
              <div className="text-center py-12">
                <div className="text-red-500 text-6xl mb-4">‚ö†Ô∏è</div>
                <h3 className="text-xl font-semibold text-gray-900 mb-2">Error Loading Products</h3>
                <p className="text-gray-600 mb-4">{error}</p>
                <div className="space-x-4">
                  <button 
                    onClick={() => window.location.reload()} 
                    className="bg-gray-900 text-white px-6 py-2 rounded-lg hover:bg-gray-800 transition-colors"
                  >
                    Try Again
                  </button>
                  <Link 
                    href="/" 
                    className="bg-gray-200 text-gray-900 px-6 py-2 rounded-lg hover:bg-gray-300 transition-colors inline-block"
                  >
                    Go Home
                  </Link>
                </div>
              </div>
            )}

            {!loading && !error && (
              <>
                {products.length > 0 && (
                  <div className="mb-6 flex justify-between items-center">
                    <p className="text-gray-600">
                      Found {products.length} product{products.length !== 1 ? 's' : ''} in {categoryName}
                    </p>
                    <div className="text-sm text-gray-500">
                      Sort by: <span className="font-medium">Popular</span>
                    </div>
                  </div>
                )}
                
                <ProductList 
                  products={products} 
                  emptyMessage={`No ${categoryName.toLowerCase()} available at the moment.`}
                />

                {products.length === 0 && (
                  <div className="text-center mt-8">
                    <Link 
                      href="/" 
                      className="bg-gray-900 text-white px-6 py-3 rounded-lg hover:bg-gray-800 transition-colors inline-block"
                    >
                      Browse All Categories
                    </Link>
                  </div>
                )}
              </>
            )}
          </div>
        </section>
      </main>

      <Footer />
    </>
  );
}

// Helper function to get category descriptions
function getCategoryDescription(category: string): string {
  const descriptions: Record<string, string> = {
    cakes: "Delicious cakes for every celebration - from birthdays to anniversaries, we have the perfect cake for your special moments.",
    flowers: "Fresh and beautiful flowers to express your feelings - roses, lilies, and seasonal blooms delivered with care.",
    gifts: "Thoughtful gifts for your loved ones - personalized items, chocolates, and unique presents for every occasion.",
    decorations: "Beautiful decorations to make your celebrations memorable - party supplies, balloons, and festive accessories."
  };
  
  return descriptions[category] || "Discover our amazing collection of products for your special occasions.";
}
